body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

@media (max-width: 1219px) {
    .nav {
        width: 270px;
    }
}

@media (min-width: 1220px) {
    .nav {
        width: 300px;
    }
}

.nav {
    background-color: #c04032;
    position: fixed;
    height: 100%;
    text-align: left;
    z-index: 1;
    overflow: auto;
    padding: 120px 10px 50px 10px;
    -webkit-transition: margin-left 0.2s;
    transition: margin-left 0.2s;
    box-shadow: 1px 0px 3px 3px rgba(0, 0, 99, 0.2)
}

.username {
    display: block;
    margin: 30px auto 4px auto;
    font-size: 1.8rem;
    font-weight: 900;
    color: rgb(10, 10, 10);
    text-align: center;
}

.role {
    display: block;
    margin: 4px auto 8px auto;
    font-size: 1.5rem;
    font-weight: 900;
    color: rgb(10, 10, 10);
    text-align: center;
}

.nav-item {
    padding: 0 50px;
    margin-top: 12px;
    text-align: right;
    font-size: 1.3em;
}

.nav-item{
    font-family: 'Oswald', Impact, Haettenschweiler, 'Arial Narrow Bold', sans-serif;
    font-weight: 700;
    text-decoration: none;
    color: rgb(40, 40, 40);
    cursor: pointer;
}

.nav-item:hover {
    text-decoration: none;
    color: rgb(200, 200, 200);
}

.nav-bar {
    position: absolute;
    width: 4px;
    height: 2em;
    background-color: rgba(255, 255, 115, 0.6);
    right: 40px;
    -webkit-transition: top 0.4s;
    transition: top 0.4s;
}

.contact {
    display: flex;
    justify-content: space-around;
    margin: 10px 20px;
}

a .contact-icon {
    color: rgb(30, 30, 30);
    font-size: 36px;
    -webkit-transition: color 0.4s;
    transition: color 0.4s;
}

a .contact-icon:hover {
    color: rgb(220, 220, 220);
}

.nav::-webkit-scrollbar {
    width: 0px;
}

#svg-img {
    -webkit-transition: x 0.2s;
    transition: x 0.2s;
    cursor: pointer;
}

@media (max-width: 349px) {
    .dis_block{
        padding: 40px 0 16px 0;
        min-height: 148px;
        border-radius: 5px;
        box-shadow: 2px 2px 2px 2px rgba(0, 0, 0, 0.2);
        display: block;
        flex-direction: column;
        justify-content: center;
        max-width: 260px;
        margin: 0 auto;
    }
    .dis_block > div{
        margin-top: 40px;
        margin-left: 12px
    }
}

@media (max-width: 519px) {
    .dis_block{
        padding: 40px 10px 16px 10px;
        min-height: 148px;
        border-radius: 5px;
        box-shadow: 2px 2px 2px 2px rgba(0, 0, 0, 0.2);
        display: block;
        flex-direction: column;
        justify-content: center;
        max-width: 330px;
        margin: 0 auto;
    }
    .dis_block .dis_children{
        margin-top: 40px;
        margin-left: 12px
    }
}

@media (min-width: 520px){
    .dis_block{
        padding: 24px 24px 24px 24px;
    }

    .dis_children{
        margin-left: 24px;
        float: left;
    }

}

.dis_block {
    min-height: 148px;
    border-radius: 5px;
    box-shadow: 2px 2px 2px 2px rgba(0, 0, 0, 0.2);
    margin: 30px auto;
    display: flex;
    align-items: center;
}

.dis_block>p {
    margin-bottom: 0px;
}

::-webkit-scrollbar {
    width: 8px;
    height: 8px;
    background-color: none;
}

::-webkit-scrollbar-thumb {
    border-radius: 8px;
    background-image: -webkit-gradient(linear, left top, left bottom, from(rgba(254, 109, 100, 0.5)), to(rgba(224, 25, 20, 0.5)));
    background-image: -webkit-linear-gradient(rgba(254, 109, 100, 0.5), rgba(224, 25, 20, 0.5));
    background-image: linear-gradient(rgba(254, 109, 100, 0.5), rgba(224, 25, 20, 0.5))
}

::-webkit-scrollbar-track {
    background-color: none;
}
@media (max-width: 519px) {
    .home-block {
        padding-left: 10px;
        text-align: left;
        min-height: 100vh;
        position: relative;
        padding-right: 10px;
    }

    .block-title {
        font-size: 3.5em;
    }
}

@media (min-width: 521px) {
    .home-block {
        padding-left: 50px;
        padding-right: 24px;
        max-width: 600px;
    }

    .block-title {
        font-size: 4.2em;
    }
}

@media (min-width: 1201px) {
    .home-block{
        max-width: 780px;
    }

    .block-title {
        font-size: 4.2em;
    }
}

.head-bar-margin {
    position: relative;
    margin-bottom: 50px;
    width: 0;
    margin-top: -2px;
    height: 0;
}

.head-bar {
    height: 48px;
    background-color: rgb(40, 40, 40);
    width: 100%;
    position: fixed;
    top: 0;
    box-shadow: 0px 0px 4px 2px rgba(0, 0, 0, 0.5);
    z-index: 10;
    font-family: 'Oswald', 'Saira Extra Condensed', Impact, Haettenschweiler, 'Arial Narrow Bold', sans-serif;
    -webkit-transition: margin-top 0.5s;
    transition: margin-top 0.5s;
}

p {
    overflow-x: hidden;
    font-size: 16px;
}

.home-block {
    padding-top: 200px;
    padding-bottom: 200px;
    text-align: left;
    position: relative;
}

.fullpage {
    min-height: 100vh;
}

.block-title {
    font-family: 'Saira Extra Condensed', Impact, Haettenschweiler, 'Arial Narrow Bold', sans-serif;
    font-weight: 800;
    color: rgb(220, 90, 100);
    display: block;
    position: absolute;
    top: 20px;
    left: 20px;
}

.block-subtitle {
    font-family: 'Saira Extra Condensed', Impact, Haettenschweiler, 'Arial Narrow Bold', sans-serif;
    color: rgb(240, 110, 120);
    font-weight: 600;
    font-size: 2em;
}

.home-block a:hover {
    text-decoration: underline;
    color: rgb(240, 120, 120);
}


.home-block a {
    color: rgb(210, 120, 120);
    -webkit-transition: -webkit-text-decoration 0.2s;
    transition: -webkit-text-decoration 0.2s;
    transition: text-decoration 0.2s;
    transition: text-decoration 0.2s, -webkit-text-decoration 0.2s;
}


